import { observable } from 'mobx';
import Peer from 'peerjs';

export class BletherState {
  public peer: Peer;
  public conn?: Peer.DataConnection;
  @observable peerId = '';
  @observable connectId = '';
  @observable status = 'Waiting';

  constructor() {
    this.peer = new Peer();
    this.peer.on('open', (id: string) => {
      this.peerId = id;
    });

    this.peer.on('connection', (conn: Peer.DataConnection) => {
      this.conn = conn;
      this.status = 'Received connection from ' + conn.peer;
    });
  }

  public setConnectionId(id: string) {
    this.connectId = id;
  }

  public connect() {
    if (!this.connectId) {
      return;
    }

    this.status = 'Connecting to ' + this.connectId;

    this.conn = this.peer.connect(this.connectId);

    this.conn.on('open', () => {
      //his.conn.send('hi');
      this.status = 'Made connection with ' + this.connectId;
    });
  }

  private receive() {}
}
